$tablet-width: 768px;
$desktop-width: 1024px;

:root {
    --max-width-d: #{$desktop-width};
}

@mixin tablet_min {
    @media (min-width: #{$tablet-width}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - 1px}) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: #{$desktop-width}) {
        @content;
    }
}
